.job-container {
    width: 100%;
}
.job-header {
    padding:0.4rem 1rem;
    margin: 0;
    background-color: var(--secondary-bg-color);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 0;
    border-top: 4px solid var(--main-bg-color);
}
.job-header div{
    display: flex;
    align-items: center;
}
.job-header div h2{
    margin-bottom:0; 
}
.job-title-left h2:nth-child(1){
    color: var(--highlight-colour);
    margin-right: 1rem;
}
.job-title-right{
    text-align: right;
}
.job-title-right h2{
    margin-right: 1rem;
    position: relative;
    top: -3px;
}
.job-title-right svg{
    height: 100%;
    width: 2rem;
}
.job-dropdown-container{
    display: flex;
    background-color: var(--secondary-bg-color);
    background-color: #272727;
    width: 100%;
    height: auto;
    margin: 0;
    padding: 1rem;
    top: 0;
    align-items: center;
}
.job-desc-container{
    padding: 0 1rem 0 2rem;
}
.job-desc-li{
    padding: 0;
    margin: 0;
}
.job-image{
    height: 10rem;
    transition: ease-in-out 0.25s;
    filter: grayscale();
}
.job-image:hover{
    filter:none;
}

@media only screen and (max-width: 767px) {
    .job-image{
        height: 7rem;
    }
}